import React from 'react';
import { Drawer, Paper, List, ListItem } from '@material-ui/core';
import { scroller} from 'react-scroll';

const SideDrawer = ({ open, onClose }) => {

  const scrollToSection = (section) => {
    scroller.scrollTo(section, {
      duration: 1200,
      delay:80,
      smooth:true,
      offset:-96
    });
    onClose(false);
  }

  return ( 
    <Drawer 
      anchor="right"
      open={open}
      onClose={() => onClose(false)}
    ><Paper style={{background: 'rgba(47, 47, 47, .85)', height: '100%', color: '#ffffff', borderRadius:'0'}}>
      <List component="nav">
        <ListItem
          button
          onClick={() => scrollToSection('featured')}
        >
          Event Starts In
        </ListItem>

        <ListItem
          button
          onClick={() => scrollToSection('venueInfo')}
        >
          Venue Info
        </ListItem>

        <ListItem
          button
          onClick={() => scrollToSection('highlights')}
        >
          Highlights
        </ListItem>

        <ListItem
          button
          onClick={() => scrollToSection('pricing')}
        >
          Pricing
        </ListItem>

        <ListItem
          button
          onClick={() => scrollToSection('location')}
        >
          Location
        </ListItem>
      </List>
    </Paper>
    </Drawer>
   );
}
 
export default SideDrawer;