import React, { Component } from 'react';
import Fade from 'react-reveal/Fade';
import Slide from 'react-reveal/Slide';
import AppButton from '../Utils/AppButton';

class Discount extends Component {
  state = { 
    discountStart:0,
    discountEnd:30
   }

  porcentageRise = () => {
    if(this.state.discountStart < this.state.discountEnd){
      this.setState({discountStart: this.state.discountStart + 1})
    }
  }

  componentDidUpdate(){
    setTimeout(() => {
      this.porcentageRise()
    },30)
  }

  render() { 
    return ( 
        <div className="center_wrapper">
          <div className="discount_wrapper">

            <Fade onReveal={() => this.porcentageRise()}>
              <div className="discount_porcentage">
                <span>{this.state.discountStart}</span>
                <span>OFF</span>
              </div>
            </Fade>

            <Slide right>
              <div className="discount_description">
                <h3>Purchase Pre-Sale Tickets</h3>
                <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Voluptatibus nostrum amet nulla asperiores quibusdam nisi consequuntur.
                </p>

                <AppButton
                  text="Purchase tickets"
                  bck="#ffa800"
                  color="#ffffff"
                  link="https://www.stubhub.co.uk"
                />

              </div>
            </Slide>

            

            

          </div>
        </div>
     );
  }
}
 
export default Discount;