import React, { Component } from 'react';
import AppButton from '../Utils/AppButton';
import Zoom from 'react-reveal/Zoom';

class Pricing extends Component {
  state = { 
    prices:[45, 60, 80],
    seats:['Back area', 'Middle area', 'Front area'],
    desc:[
      'Lorem, ipsum dolor sit amet consectetur adipisicing elit. Quidem eveniet odio illum minima voluptatem aperiam molestias.',
      'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Autem aspernatur rerum dicta delectus vel.',
      'Lorem ipsum dolor sit amet consectetur adipisicing elit. Corporis reiciendis molestias fuga labore praesentium obcaecati.'
    ],
    linkTo:[
      'https://www.stubhub.co.uk', 'https://www.ticketmaster.co.uk/', 'https://vipnation.eu/'
    ],
    delay:[400, 0, 600]
   }


   showBoxes = () => (
      this.state.prices.map((price, i) => (
        <Zoom key={i} delay={this.state.delay[i]}>
          <div className="pricing_item">
            <div className="pricing_inner_wrapper">
              <div className="pricing_title">
                <span>{price}</span>
                <span>{this.state.seats[i]}</span>
              </div>
              <div className="pricing_description">
                {this.state.desc[i]}
              </div>
              <div className="pricing_buttons">
                <AppButton
                  text="Purchase"
                  bck="#ffa800"
                  color="#ffffff"
                  link={this.state.linkTo[i]}
                />
              </div>
            </div>
          </div>
        </Zoom>
       
      ))

     
   )

  render() { 
    return ( 
      <div className="bck_black">
        <div className="center_wrapper pricing_section">
          <h2>Pricing</h2>

          <div className="pricing_wrapper">
            {this.showBoxes()}
          </div>
        </div>
      </div>
     );
  }
}

export default Pricing;