import React from 'react';
import Slider from 'react-slick';
import slide1 from '../../resources/images/quanticandalice.jpg';
import slide2 from '../../resources/images/Alice-Russell-620x340.jpg';
import slide3 from '../../resources/images/quantic_082211.jpg';


const Carrousel = () => {

  const settings = {
    dots: false,
    infinite: true,
    speed: 600,
    autoplay: true
  };

  return ( 
    <div className="carrousel_wrapper" style={{overflow:'hidden', background: "rgba(47, 47, 47, .8)", height: "100vh"}}>
      <Slider {...settings}>

        <div className="carrousel_image">
          <img src={slide1} alt="slide1" style={{width:"100%", height:"100vh" }}/>
        </div>

        <div className="carrousel_image">
          <img src={slide2} alt="slide2" style={{width:"100%", height:"100vh" }}/>
        </div>
       
        <div className="carrousel_image">
          <img src={slide3} alt="slide3" style={{width:"100%", height:"100vh" }}/>
        </div>
       
      </Slider>
    </div>
   );
}
 
export default Carrousel;